html, body { margin:0; padding:0; overflow:hidden; font-family:sans-serif; }
#ui { position:fixed; top:0; left:0; width:100%; z-index:10; pointer-events:none; }
#model-picker { display:flex; gap:8px; padding:8px; pointer-events:auto; }
#model-picker img { width:60px; height:60px; object-fit:cover; border:2px solid #999; border-radius:6px; cursor:pointer; }
#model-picker img.selected { border-color:#00adee; }
#object-tools { position:fixed; bottom:20px; left:50%; transform:translateX(-50%); pointer-events:auto; }
#delete-btn { padding:10px 16px; font-size:14px; }
#hint { position:fixed; bottom:0; left:0; width:100%; text-align:center; padding:8px; background:rgba(0,0,0,.4); color:#fff; font-size:12px; pointer-events:none; }
.hidden { display:none; }
#xr-canvas { width:100vw; height:100vh; display:block; }
